.header {
	background-color: #18181b;
	padding: 20px;
	box-shadow: 0 -4px 2px 6px black;
	z-index: 1;
}

.container {
	width: 60%;
	margin: 0 auto;

	display: flex;
	justify-content: space-between;
}

@media screen and (max-width: 1200px) {
	.container {
		width: 80%;
	}
}

@media screen and (max-width: 768px) {
	.container {
		width: 100%;
		padding: 20px;
	}
}